    width: 16px;
    height: 16px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    border: 5px solid transparent;
    border-top-color: #ffff;
    border-radius: 50%;
    animation: button_button_loading_spinner__1W9Aq 1s ease infinite;
}
